
.wrapper {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
}
.news {
    display: flex;
    flex-wrap: wrap;
    @media (max-width: 950px) {
        flex-direction: column;
    }
}
.news-item {
    line-height: 16pt;
    position: relative;
    display: flex;
    padding: 10px;
    max-width: 1060px;
    flex: 0 1 50%;
    justify-self: center;
    min-width: 360px;
}
.news-item__row {
    background-color: #f1f0ef;
    display: flex;
    width: 100%;
    @media (max-width: 420px) {
        display: flex;
        flex-direction: column-reverse;
    }
}

.news-item__content {
    display: flex;
    flex-direction: column;
    min-width: 1px;
    /* word-break: break-all;  */  /*use this*/
    /* word-wrap: break-word; */
    margin: 5px;
    @media (max-width: 576px) {
        display: inline;
    }
}
.news-item__title {
    font-size: 21px;
    padding: 0 0 0 5pt;
}
.news-item__time {
    font-size: 14px;
    color: #474651;
    margin: 5px 0 5px 20pt;
}
.news-item__body {
    font-size: 16px;
    padding: 0 0 0 5pt;
}